    }
    
    .pl-bolder-text{
        font-size: 5vw;
        font-weight: 00;
    }
    
    .pl-list{
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
}
.video-box {
  width: 100%;
  height: 100%;
  position: relative;
  display: flex;
  justify-content: center;
  overflow: hidden;
}

.thumbnail {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: contain;
  opacity: 1;
  transition: opacity 0.3s; /* Fade in the video over 0.3 seconds */
}

.video {
  width: 100%;
  height: 100%;
  object-fit: contain;
  opacity: 0; /* Hide the video */
  transition: opacity 0.3s; /* Fade in the video over 0.3 seconds */
}

.video-box:hover .thumbnail {
  opacity: 0; /* Fade out the thumbnail on hover */
}

.video-box:hover .video {
  opacity: 1; /* Fade in the video on hover */
}

.p{
    width: 100%;
    height: 45vw;
    display: flex;
    justify-content: center;
    align-items: center;
}

.p-card{
    width: 85%;
    height: 85%;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 20px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.3); 
}

.p-left {
    flex: 1 1;
    order: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-left: 2vw;
}

.p-right {
    flex: 1 1;
    order: 2;
    display: flex;
    align-items: center;
    padding-right: 2vw;
}

.p-parent{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.p-box{
    width: 35vw;
    border: solid rgb(238, 231, 231) 3px;
    border-radius: 10px 10px 0px 0px;
    justify-content: center;
    align-items: center;
}

.p-browser{
    height: 2.3vh;
    background-color: rgb(238, 231, 231);
    align-items: center;
    display: flex;
}


.p-circle{
    width: 8px;
    height: 8px;
    background-color: white;
    border-radius: 50%;
    margin: 3px;
}

.p-texts-right{
    width: 100%;
    padding: 2% 5%;
    text-align: left;
}

.p-texts-left{
    width: 100%;
    padding-right: 1%;
    text-align: left;
}

.p-title{
    padding-top: 15px;
    width: 100%;
    font-size: 2vw;
    font-weight: 500;
}

.p-desc{
    font-size: 1.2vw;
    font-weight: 300;
    padding: 1vh 0% 0% 0%;
}

.p-subtitle{
    width: 100%;
    font-size: 1.5vw;
    font-weight: 450;
    padding: 2vh 0% 0% 0%;
}

@media screen and (max-width: 550px) {
    .p{
        height: 100%;
    }
    .p-card{
        margin: 5%;
        width: 100%;
        height: 100%;
        flex-direction: column;
    }
    .p-left{
        padding: 0;
        order: 1;
        width: 80%;
        height: 50%;
        justify-content: center;
    }
    .p-right{
        padding: 0;
        order: 1;
        width: 80%;
        height: 50%;
        justify-content: center;
    }
    .p-parent{
        width: 100%;
    }
    .p-box{
        position: relative;
        margin-top: 5%;
        width: 100%;
        height: 100%;
    }
    .p-texts-right{
        padding: 5%;
    }
    .p-texts-left{
        padding: 5%;
    }
    .p-title{
        font-size: 5vw;
    }
    .p-desc{
        font-size: 3vw;
    }
    .p-subtitle{
        font-size: 4vw;
    }
}
.c {
  height: 100vh;
  position: relative;
}

.c-bg {
  width: 20px;
  height: 100vh;
  background-color: forestgreen;
  position: absolute;
}

.c-wrapper {
  padding: 50px;
  display: flex;
}

.c-left {
  flex: 1 1;
}

.c-title {
  font-weight: bolder;
  font-size: 5vw;
  width: 80%;
}

.c-info-items {
  display: flex;
  flex-direction: column;
  width: 30vw;
}

.c-phone {
  font-size: 1.5vw;
  margin-bottom: 1vw;
}

.c-email {
  font-size: 1.5vw;
}

.c-right {
  flex: 1 1;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.c-desc {
  font-weight: 200;
  font-size: 1.5vw;
}

form {
  margin-top: 20px;
}

input {
  width: 25vw;
  height: 4vw;
  border: none;
  border-bottom: 1px solid black;
  margin: 10px 0px;
  font-size: 1vw;
  padding-left: 10px;
}

textarea {
  width: 45vw;
  margin: 10px 0px;
  font-size: 1vw;
  padding-left: 10px;
}

button {
  border: 0;
  padding: 15px;
  background-color: forestgreen;
  color: white;
  font-weight: 500;
  font-size: 1vw;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}

button:disabled {
  background-color: #555;
  cursor: not-allowed;
  box-shadow: none;
  transform: none;
}

button:not(:disabled):hover {
  box-shadow: 0 10px 10px 0 rgba(0, 0, 0, 0.24),
    0 17px 50px 0 rgba(0, 0, 0, 0.19);
  transform: scale(1.03);
}

button:not(:disabled):active {
  transform: scale(0.97);
  background-color: rgb(26, 104, 26);
}

.c-done {
  color: forestgreen;
  font-size: 1.5vw;
  margin-top: 10px;
  font-weight: 500;
}

@media screen and (max-width: 550px) {
  .c-wrapper {
    flex-direction: column;
    padding: 10vw;
  }

  .c-title {
    font-size: 10vw;
  }
  .c-bg {
    height: 100vh;
  }

  .c-info-items {
    margin: 10px 0px;
    width: 70%;
  }

  .c-phone {
    font-size: 3vw;
  }

  .c-email {
    font-size: 3vw;
  }

  .c-desc {
    font-size: 3vw;
  }

  form {
    margin-top: 0px;
    display: flex;
    flex-wrap: wrap;
  }

  input {
    width: 70%;
    font-size: 3vw;
    height: 40px;
    margin: 10px;
    margin-left: 0px;
  }

  textarea {
    width: 70%;
    font-size: 3vw;
    margin: 10px;
    margin-left: 0px;
  }

  button {
    width: 30%;
    font-size: 3vw;
    margin: 10px;
    margin-left: 0px;
  }

  .c-done {
    font-size: 4vw;
  }
}

.timeline {
  width: 100%;
  display: flex;
  flex-direction: column;
}

.timeline-title {
  font-size: 6vw;
  font-weight: 600;
  text-align: center;
}

.vertical-timeline-element-title {
  font-weight: 600;
  font-size: 1.5vw;
  margin-bottom: 10px;
}

.vertical-timeline-element-content {
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.3);
  border-radius: 20px;
  width: 90%;
}

.vertical-timeline-element-subtitle {
  font-size: 1.2vw;
  color: #555;
  font-weight: 500;
  margin-bottom: 10px;
}

.vertical-timeline {
  max-width: 100vw;
}

.vertical-timeline--two-columns
  .vertical-timeline-element-content
  .vertical-timeline-element-date {
  font-size: 1.1vw;
  font-weight: 300;
}

.vertical-timeline-element-content li,
.vertical-timeline-element-content p {
  font-size: 1vw;
  font-weight: 300;
  margin: 0px;
}

.vertical-timeline-element-content ul {
  padding-left: 15px;
}

.timeline-icon {
  background: #228b22;
  box-shadow: 0 0 0 4px #228b22;
  display: flex;
  align-items: center;
  justify-content: center;
}

.timeline-svg-icon {
  object-fit: cover;
  width: 100%;
  height: 100%;
  border-radius: 50%;
}

@media screen and (max-width: 550px) {
  .timeline {
    margin-top: 10vw;
  }

  .timeline-title {
    font-size: 10vw;
    font-weight: 600;
  }

  .vertical-timeline-element-title {
    font-size: 5vw;
  }

  .vertical-timeline-element-content {
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.3);
    border-radius: 20px;
    width: 75vw;
    padding-right: 5vw;
  }

  .vertical-timeline-element-subtitle {
    font-size: 4vw;
  }

  .vertical-timeline-element-content li,
  .vertical-timeline-element-content p {
    font-size: 3vw;
  }

  .vertical-timeline-element-content ul {
    padding-left: 15px;
  }

  .vertical-timeline--two-columns
    .vertical-timeline-element-content
    .vertical-timeline-element-date {
    font-size: 2vw;
    font-weight: 300;
  }
}

